const Rarity = (props) => {
	return (
		<div
			className="mb-1 col-12"
			style={{ position: "relative", display: "inline-block", width: "60%" }}
		>
			<div
				style={{
					marginTop: "2px",
					position: "absolute"
				}}
				className="col-8"
			>
				<p
					style={{ fontSize: "13px" }}
					className="tetx-light text-center ms-0 ms-md-3 ms-lg-2"
				>
					{props.plantID}
				</p>
			</div>
			<img
				style={{ display: "block", maxWidth: "100%", height: "auto" }}
				src={process.env.PUBLIC_URL + `/assets/rarity/${props.rarity}.svg`}
				alt={props.rarity}
			/>
		</div>
	);
};

export default Rarity;
