export const elementCounter = (garden) => {
	let elementCount = {
		fire: 0,
		wind: 0,
		water: 0,
		electro: 0,
		dark: 0,
		light: 0,
		metal: 0,
		parasite: 0,
		ice: 0
	};
	garden.forEach((plant) => {
		if (plant.type == "Plant") {
			// eslint-disable-next-line default-case
			switch (plant.element) {
				case "Fire":
					elementCount.fire++;
					return;
				case "Wind":
					elementCount.wind++;
					return;
				case "Water":
					elementCount.water++;
					return;
				case "Electro":
					elementCount.electro++;
					return;
				case "Dark":
					elementCount.dark++;
					return;
				case "Light":
					elementCount.light++;
					return;
				case "Metal":
					elementCount.metal++;
					return;
				case "Parasite":
					elementCount.parasite++;
					return;
				case "Ice":
					elementCount.ice++;
					return;
			}
		}
	});
	return elementCount;
};

const synergyCalculate = (elementCount) => {
	let buff = {
		IceProductivity: 0,
		WaterProductivity: 0,
		MetalProductivity: 0,
		DarkBuff: 0,
		WindBuffTime: 0,
		ParaBuff: 0
	};
	if (elementCount.ice >= 5) {
		buff.IceProductivity += 0.1;
	}
	if (elementCount.water >= 8) {
		buff.WaterProductivity += 0.5;
	}
	if (elementCount.metal >= 10) {
		buff.MetalProductivity += 0.2;
	}
	if (elementCount.dark >= 5) {
		buff.DarkBuff += parseInt(elementCount.dark / 5) * 0.05;
	}
	if (elementCount.wind >= 10) {
		buff.WindBuffTime += 0.1;
	}
	if (elementCount.parasite >= 10) {
		buff.ParaBuff += 0.2;
	}
	return buff;
};

export const applySynergy = (team, elementCount) => {
	let buffTeam = [...team];
	let buff = synergyCalculate(elementCount);
	buffTeam.forEach((plant) => {
		if (plant.element) {
			plant.LEBuff = 0;
			plant.hourReduced = 0;
		}
	});
	if (buff.IceProductivity > 0) {
		buffTeam.forEach((plant) => {
			if (plant.element == "Ice") {
				plant.LEBuff = plant.LE * buff.IceProductivity;
			}
		});
	}
	if (buff.WaterProductivity > 0) {
		buffTeam.forEach((plant) => {
			if (plant.element == "Water") {
				plant.LEBuff = plant.LE * buff.WaterProductivity;
			}
		});
	}
	if (buff.MetalProductivity > 0) {
		buffTeam.forEach((plant) => {
			if (plant.element == "Metal") {
				plant.LEBuff = plant.LE * buff.MetalProductivity;
			}
		});
	}
	if (buff.DarkBuff > 0) {
		buffTeam.forEach((plant) => {
			if (plant.element) {
				plant.LEBuff = plant.LE * buff.DarkBuff;
				return plant;
			}
		});
	}
	if (buff.ParaBuff > 0) {
		buffTeam.forEach((plant) => {
			if (plant.element) {
				plant.LEBuff = plant.LE * buff.ParaBuff;
				return plant;
			}
		});
	}
	if (buff.WindBuffTime > 0) {
		buffTeam.forEach((plant) => {
			if (plant.element) {
				return (plant.hourReduced = plant.hour * buff.WindBuffTime);
			}
		});
	}
	// console.log(buffTeam);
};

const ElementSynergy = () => {
	return <></>;
};

export default ElementSynergy;
