/* eslint-disable eqeqeq */
import moment from "moment";
import { useEffect, useState } from "react";
import { ToolsList } from "../plantAssets/ToolsList";

function toFixed(value, precision) {
	var power = Math.pow(10, precision || 0);
	return String(Math.round(value * power) / power);
}

const toolPriceCalculate = ({
	totalBigPotUsed,
	totalSmallPotUsed,
	totalWatered,
	scareCrowUsed,
	greenHouseUsed
}) => {
	let ToolSum = 0;
	let PotSum = 0;
	ToolsList.forEach((tool, index) => {
		let price = tool.price;
		// eslint-disable-next-line default-case
		switch (index) {
			case 0:
				ToolSum += scareCrowUsed * price;
				break;
			case 1:
				ToolSum += totalWatered * price;
				break;
			case 4:
				ToolSum += greenHouseUsed * price;
				break;
			case 3:
				PotSum += totalSmallPotUsed * price;
				break;
			case 2:
				PotSum += totalBigPotUsed * price;
				break;
		}
	});
	return {
		PotSum,
		ToolSum
	};
};

const TotalBoard = (props) => {
	const [price, setPrice] = useState(0);
	const [udpateTime, setUdpateTime] = useState(0);
	const [rate, setRate] = useState(150);
	const [oldPrice, setOldPrice] = useState(24);
	useEffect(() => {
		const getAPI = async () => {
			let res = await fetch(
				"https://api.pancakeswap.info/api/v2/tokens/0x31471e0791fcdbe82fbf4c44943255e923f1b794"
			);
			let data = await res.json();
			setPrice(toFixed(data.data.price, 2));
			setUdpateTime(moment(data.updated_at).fromNow());
		};
		getAPI();
	});

	let {
		totalSapling,
		totalMama,
		totlaLEPerHour,
		totalPlant,
		totalBigPotUsed,
		totalSmallPotUsed,
		totalWatered,
		scareCrowUsed,
		greenHouseUsed
	} = props.data;
	let tax = 95 / 100;
	let LEtoPVU = 500;
	totlaLEPerHour = totlaLEPerHour * tax;
	let totlaPVUPerHour = totlaLEPerHour / LEtoPVU;
	let totalPrice = totlaPVUPerHour * price;
	let totalSpendForSunflower = totalSapling * 100 + totalMama * 200;
	let { PotSum, ToolSum } = toolPriceCalculate({
		totalBigPotUsed,
		totalSmallPotUsed,
		totalWatered,
		scareCrowUsed,
		greenHouseUsed
	});
	let totalSpend = ToolSum + PotSum + totalSpendForSunflower;
	return (
		<div
			style={{ minHeight: "100px" }}
			className="col-12 col-md-6 flex-row d-flex text-light mt-3"
		>
			{/* <div className="col-12 col-md-10 ms-2">
				<p>
					Total: {totalPlant - totalSapling - totalMama} NFT - {totalSapling}{" "}
					sapling, {totalMama} mama ({totalPlant})
				</p>
			</div> */}
			<div className="col-12 col-md-10">
				<p className="text-center h5">Spend</p>
				<div className="d-flex col-10">
					For sunflowers:
					<span
						className={`text-${
							totalSpendForSunflower == 0 ? "light" : "danger"
						} ms-auto`}
					>
						{totalSpendForSunflower}
					</span>
				</div>
				<div className="d-flex col-10">
					For pots:{" "}
					<span className={`text-${PotSum == 0 ? "light" : "danger"} ms-auto`}>
						{PotSum}
					</span>
				</div>
				<div className="d-flex col-10">
					For other tools:{" "}
					<span className={`text-${ToolSum == 0 ? "light" : "danger"} ms-auto`}>
						{ToolSum}
					</span>
				</div>
				<div className="d-flex col-10">
					Total LE spend:{" "}
					<span
						className={`text-${totalSpend == 0 ? "light" : "danger"} ms-auto`}
					>
						{totalSpend}
					</span>
				</div>
				<div className="d-flex col-10">
					Total PVU spend:{" "}
					<span
						className={`text-${
							totalSpend == 0 ? "light" : "danger"
						} ms-auto me-1`}
					>
						{toFixed(totalSpend / rate, 2)}
					</span>
					~
					<span className={`text-${totalSpend == 0 ? "light" : "danger"} ms-1`}>
						{toFixed((totalSpend / rate) * oldPrice, 2)}$
					</span>
				</div>
				<div className="col-10 d-flex">
					PVU buy price:{" "}
					<input
						value={oldPrice}
						onChange={(e) => setOldPrice(e.target.value)}
						max="30"
						type="number"
						style={{ width: "30%" }}
						className="form-control form-control-sm bg-dark text-light ms-3 border-white"
					/>
					<span className="mt-1">$</span>
				</div>
			</div>
			<div className="col-12 col-md-10">
				<p className="text-center h5">Income</p>
				<div className="d-flex col-12">
					LE per hour:
					<span
						className={`text-${totlaLEPerHour == 0 ? "light" : ""} ms-auto`}
						style={{ color: "#20c997" }}
					>
						{toFixed(totlaLEPerHour, 2)}
					</span>
				</div>
				<div className="d-flex col-12">
					LE per day:
					<span
						className={`text-${totlaLEPerHour == 0 ? "light" : ""} ms-auto`}
						style={{ color: "#20c997" }}
					>
						{toFixed(totlaLEPerHour * 24, 2)}
					</span>
				</div>
				<div className="d-flex col-12">
					LE to PVU per day
					<span
						className={`text-${totlaPVUPerHour == 0 ? "light" : ""} ms-auto`}
						style={{ color: "#20c997" }}
					>
						{toFixed(totlaPVUPerHour * 24, 2)} PVU
					</span>
				</div>
				<div className="d-flex col-12">
					Total income:{" "}
					<span
						className={`text-${totalPrice == 0 ? "light" : ""} ms-auto`}
						style={{ color: "#20c997" }}
					>
						{toFixed(totalPrice * 24, 2)}$
					</span>
				</div>
				<div className="text-warning">
					Income not inclued spend on sunflowers and tools
				</div>
				<div className="text-warning">500 LE = 1 PVU</div>
				<div className="col-12 text-center">
					1 PVU ={" "}
					<span className="ms-auto" style={{ color: "#20c997" }}>
						{price}$
					</span>{" "}
					<p>updated {udpateTime}</p>
				</div>
			</div>
			{/* <div className="ms-2 mt-2">
				<small>Sorry for bad UI. Formula will update</small>
			</div> */}
		</div>
	);
};

export default TotalBoard;
